import React, { useState, useRef, useEffect} from 'react';
import ReactFileReader from 'react-file-reader';
import './App.css';
import './AppMobile.css';
import { CSVLink } from "react-csv";
import { Button } from '@material-ui/core';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import TextField from '@material-ui/core/TextField';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';  
import {Helmet} from 'react-helmet'
import Chip from '@material-ui/core/Chip';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import ColorThief from '../node_modules/colorthief/dist/color-thief.mjs';
const { createCanvas, loadImage } = require('canvas')


const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    listStyle: 'none',
    padding: theme.spacing(0.5),
    margin: 0,
  },
  chip: {
    margin: theme.spacing(0.5),
  },
}));

 
function App() {
  
  let [file, setFile] = useState('')
  let [cmWidth,setCmWidth] = useState(46)
  let [displayRatio,setDisplayRatio] = useState(1)
  const canvasRef = useRef(null)
  let [parentCanvasWidth,setParentCanvasWidth] = useState(800)
  let [isDoneTiling,setIsDoneTiling] = useState(true)
  const canvasParentRef = useRef(null);
  const firstUpdate = useRef(true);
  let [csvData,setcsvData] = useState([])
  let [hexInput,setHexInput] = useState('#')
  const classes = useStyles();
  const [chipData, setChipData] = useState([]); 
  const [colorQuant, setColorQuant] = useState(5); 
  const [isManual, setIsManual] = useState(false);
  const [isGrid, setIsGrid] = useState(true)
  const [finalUrl, setFinalUrl] = useState('placeholder')
  let [isDoneExporting,setIsDoneExporting] = useState(true)
  

  //removes a chip if it is deleted by the user
  const handleDelete = (chipToDelete) => () => {
    setChipData((chips) => chips.filter((chip) => chip.key !== chipToDelete.key));
  };
  //tracks what is being typed in the hex field
  const onHexInputChange = (e) => {
    setHexInput(e.target.value)
  }
  
  //onclick function for the add button to add the hex code to the chip array
  const hexPush = () => {
    if(/^#([0-9A-F]{3}){1,2}$/i.test(hexInput)){
      let uid = Math.floor((Math.random())*100000000)
      setChipData([...chipData, {key: uid, label: hexInput}])
      // let updated = colorQuant + 1
      // setColorQuant(updated)
    }
  }
  //handles the dice in the input field and updates cmwidth,which then calls the dicemaker function
  const onChange = (e) => {
    let numberOfDice
    //checks input is an integer
    let input = parseInt(e.target.value)
    //too many dice alert
    if(input>=500) {alert('Please enter a number less than 500')}
    //if input is an integer and less than 200
    input < 500 && input  > 0 ? numberOfDice = input : numberOfDice = 1
    setCmWidth(parseFloat((numberOfDice*1.6).toFixed(1)))
  }
  //
  const onColorQtyChange = (e) => {
    let input = parseInt(e.target.value)
    input > 0 ? setColorQuant(input) : alert('Please enter a number greater than 1')

  }
  const handleChange = () => {
    setIsManual(isManual ? false : true);
  };
  //loads the files initially and stores src
  const handleFiles = files => {
    const img = new Image()
    img.src = files.base64
    setFile(img.src)
  }

  const manualMakeMosaic = () => {
    //adjust canvas size to based on screen width (smaller for mobile)
    setParentCanvasWidth(canvasParentRef.current ? canvasParentRef.current.offsetWidth : 0)
    //if there's at least two colors in chip array
    if(chipData.length > 1){
      //start the 'tiling...' message
      setIsDoneTiling(false)
      const img = new Image()
      img.src = file
      img.onload = () => {
        //creating the canvas for photomosaic
        const canvas = canvasRef.current
        const context = canvas.getContext('2d')
        let imgWidth = img.naturalWidth
        let imgHeight = img.naturalHeight
        //ratio needed to calculate length of the mosiac
        let ratio = imgHeight/imgWidth
        //needed to update the text displayed for length in return statement
        setDisplayRatio(ratio)
        //dice are 1.6 cm wide, cmWidth is the default number used to calc mosiac size in cms
        let numDiceWidth = Math.ceil(cmWidth / 1.6)
        let numDiceHeight = Math.ceil(ratio * cmWidth / 1.6)
        //needed to calculate the size of the dice images in context of the canvas width
      
        let size = Math.ceil(parentCanvasWidth / numDiceWidth)
        //sets canvas size to expected dimensions of mosiac
        canvas.width = size * numDiceWidth
        canvas.height = size * numDiceHeight
        //reduce image to size where 1 pixel = 1 die
        context.drawImage(img,0,0,numDiceWidth,numDiceHeight);
        //place reduced image into array for the for loop
        let imgPixels = context.getImageData(0, 0, numDiceWidth, numDiceHeight);
        //clears canvas of the drawn image
        context.clearRect(0, 0, numDiceWidth, numDiceHeight);
        //makes it so you dont need to type impixels.data in the for loop
        let data = imgPixels.data
        let indexRows = []
        for(let y = 0; y < imgPixels.height; y++){
          let indexColumns = []
          for(let x = 0; x < imgPixels.width; x++){
          //find current row by calc-ing the length and the current count
          let currentRow = (4 * imgPixels.width) * y 
          // find current index within the row
          let currentPixelInRow = (x * 4)
          // sum both in order to find your index in the current row
          let i = currentRow + currentPixelInRow
          //getting the RGB
          let r = data[i]
          let g = data[i+1]
          let b = data[i+2]
          //convert RGB to hex
          let hex = '#' + [r, g, b].map(x => {
            const hex = x.toString(16)
            return hex.length === 1 ? '0' + hex : hex
          }).join('')
          //create array of tile colors
          let fillColors = chipData.map(colorObj => colorObj.label);
          //set up function to find nearest color from provided tile array
          let nearestColor = require('nearest-color').from(fillColors);
          //calculates postion of dice based on x and y in for loop
          let xnew = x * size
          let ynew = y * size
          //draws dice image at the desired locations
          let lineWidth = Math.floor(size/2.25) > 0 ?  Math.floor(size/2.25) : 1
          let circle  = new Path2D()
          circle.arc(xnew+(size/2), ynew+(size/2), (size/2)-(lineWidth/2), 0, 2 * Math.PI, false)
          context.lineWidth = lineWidth
          context.strokeStyle = nearestColor(hex)
          context.stroke(circle)

          //adds the hex to the csv row
          indexColumns.push(nearestColor(hex))
        }
        //adds row of hex values to the csvData array (each row is a row in a csv)
        indexRows.push(indexColumns)
      }
      //this sets the csvData variable to the data created from the most recent tiling
      setIsDoneTiling(true)
      setcsvData(indexRows)

      if(isGrid){
        let shift = size * 29 
        let w = size * numDiceWidth
        let h = size * numDiceHeight
        context.strokeStyle = '#00008B'
        context.beginPath()
        for (let x = shift; x < w; x += shift) {
					context.moveTo(x,0)
					context.lineTo(x,h)
				}
        for (let y = shift; y < h; y += shift) {
					context.moveTo(0,y)
					context.lineTo(w,y)
        }
        context.lineWidth = 1
        context.stroke()
      }
      
      }
      //this deactivates the 'tiling...' message after the for loop is done
    }
    else{
      alert('Please add at least two colors')
    }
          
    
  }
  const autoMakeMosaic = () =>  {
    //adjust canvas size to based on screen width (smaller for mobile)
    setParentCanvasWidth(canvasParentRef.current ? canvasParentRef.current.offsetWidth : 0)
    //if there's at least two colors in chip array
    if(colorQuant > 1){
      //start the 'tiling...' message
      setIsDoneTiling(false)
      
      
      const img = new Image()
      img.src = file
      img.onload = () => {
        const colorThief = new ColorThief()
        const rgbColors =  colorThief.getPalette(img,colorQuant)
        let hexColors = rgbColors.map(rgb => {
            return( '#' + rgb.map(x => {
              const hex = x.toString(16)
              return hex.length === 1 ? '0' + hex : hex
            }).join(''))
        })
        let hexObjs = hexColors.map(color => {
          return (
            {
              key: Math.floor(Math.random() * Math.floor(100000)),
              label: color.toUpperCase()
            }
          )
        })
        //creating the canvas for photomosaic
        const canvas = canvasRef.current
        const context = canvas.getContext('2d')
        let imgWidth = img.naturalWidth
        let imgHeight = img.naturalHeight
        //ratio needed to calculate length of the mosiac
        let ratio = imgHeight/imgWidth
        //needed to update the text displayed for length in return statement
        setDisplayRatio(ratio)
        //dice are 1.6 cm wide, cmWidth is the default number used to calc mosiac size in cms
        let numDiceWidth = Math.ceil(cmWidth / 1.6)
        let numDiceHeight = Math.ceil(ratio * cmWidth / 1.6)
        //needed to calculate the size of the dice images in context of the canvas width
      
        let size = Math.ceil(parentCanvasWidth / numDiceWidth)
        //sets canvas size to expected dimensions of mosiac
        canvas.width = size * numDiceWidth
        canvas.height = size * numDiceHeight
        //reduce image to size where 1 pixel = 1 die
        context.drawImage(img,0,0,numDiceWidth,numDiceHeight);
        //place reduced image into array for the for loop
        let imgPixels = context.getImageData(0, 0, numDiceWidth, numDiceHeight);
        //clears canvas of the drawn image
        context.clearRect(0, 0, numDiceWidth, numDiceHeight);
        //makes it so you dont need to type impixels.data in the for loop
        let data = imgPixels.data
        let indexRows = []
        for(let y = 0; y < imgPixels.height; y++){
          let indexColumns = []
          for(let x = 0; x < imgPixels.width; x++){
          //find current row by calc-ing the length and the current count
          let currentRow = (4 * imgPixels.width) * y 
          // find current index within the row
          let currentPixelInRow = (x * 4)
          // sum both in order to find your index in the current row
          let i = currentRow + currentPixelInRow
          //getting the RGB
          let r = data[i]
          let g = data[i+1]
          let b = data[i+2]
          //convert RGB to hex
          let hex = '#' + [r, g, b].map(x => {
            const hex = x.toString(16)
            return hex.length === 1 ? '0' + hex : hex
          }).join('')
          //create array of tile colors
          // let fillColors = chipData.map(colorObj => colorObj.label);
          
          //set up function to find nearest color from provided tile array
          let nearestColor = require('nearest-color').from(hexColors);
          //calculates postion of dice based on x and y in for loop
          let xnew = x * size
          let ynew = y * size
          //draws dice image at the desired locations
          let lineWidth = Math.floor(size/2.25) > 0 ?  Math.floor(size/2.25) : 1
          let circle  = new Path2D()
          circle.arc(xnew+(size/2), ynew+(size/2), (size/2)-(lineWidth/2), 0, 2 * Math.PI, false)
          context.lineWidth = lineWidth
          context.strokeStyle = nearestColor(hex)
          context.stroke(circle)
          //adds the hex to the csv row
          indexColumns.push(nearestColor(hex))
        }
        //adds row of hex values to the csvData array (each row is a row in a csv)
        indexRows.push(indexColumns)
      }
      //this sets the csvData variable to the data created from the most recent tiling
      setChipData(hexObjs)
      setcsvData(indexRows)
      //this deactivates the 'tiling...' message after the for loop is done
      setIsDoneTiling(true)

      if(isGrid){
        let shift = size * 29 
        let w = size * numDiceWidth
        let h = size * numDiceHeight
        context.strokeStyle = '#00008B'
        context.beginPath()
        for (let x = shift; x < w; x += shift) {
					context.moveTo(x,0)
					context.lineTo(x,h)
				}
        for (let y = shift; y < h; y += shift) {
					context.moveTo(0,y)
					context.lineTo(w,y)
        }
        context.lineWidth = 1
        context.stroke()
      }
      }
    }		
  }


  //monitors changes in any of the variables in the array and calls the dice mosiac maker
  useEffect( () => {
    if(firstUpdate.current) {
      //ensures that create preview isn't called during the first loading of the components when there is no image file yet
      firstUpdate.current = false;
      }
    else {
      if(!isManual){
        autoMakeMosaic()
      }
    }
  }, [file,colorQuant,cmWidth,parentCanvasWidth,isManual,isGrid]) 

  useEffect( () => {
    if(firstUpdate.current) {
      //ensures that create preview isn't called during the first loading of the components when there is no image file yet
      firstUpdate.current = false;
      }
    else {
      if(isManual){
        manualMakeMosaic()
      }
    }
  }, [file,cmWidth,parentCanvasWidth, isManual, chipData]) 

  const makeDownload = () => {

    if(colorQuant > 1){
      setIsDoneExporting(false)
      //start the 'tiling...' message
      const img = new Image()
      img.src = file
      img.onload = () => {
        const colorThief = new ColorThief()
        const rgbColors =  colorThief.getPalette(img,colorQuant)
        let hexColors = rgbColors.map(rgb => {
            return( '#' + rgb.map(x => {
              const hex = x.toString(16)
              return hex.length === 1 ? '0' + hex : hex
            }).join(''))
        })
        //creating the canvas for photomosaic
        let imgWidth = img.naturalWidth
        let imgHeight = img.naturalHeight
        
        //ratio needed to calculate length of the mosiac
        let ratio = imgHeight/imgWidth
        //needed to update the text displayed for length in return statement

        //dice are 1.6 cm wide, cmWidth is the default number used to calc mosiac size in cms
        let numDiceWidth = Math.ceil(cmWidth / 1.6)
        let numDiceHeight = Math.ceil(ratio * cmWidth / 1.6) 

        const canvas = createCanvas(numDiceWidth, numDiceHeight)
        const context = canvas.getContext('2d')
        //needed to calculate the size of the dice images in context of the canvas width
        let size = 50
        //sets canvas size to expected dimensions of mosiac
        canvas.width = size * numDiceWidth
        canvas.height = size * numDiceHeight
        //reduce image to size where 1 pixel = 1 die
        context.drawImage(img,0,0,numDiceWidth,numDiceHeight);
        //place reduced image into array for the for loop
        let imgPixels = context.getImageData(0, 0, numDiceWidth, numDiceHeight);
        //clears canvas of the drawn image
        context.clearRect(0, 0, numDiceWidth, numDiceHeight);
        //makes it so you dont need to type impixels.data in the for loop
        let data = imgPixels.data
        let indexRows = []
        for(let y = 0; y < imgPixels.height; y++){
          let indexColumns = []
          for(let x = 0; x < imgPixels.width; x++){
          //find current row by calc-ing the length and the current count
          let currentRow = (4 * imgPixels.width) * y 
          // find current index within the row
          let currentPixelInRow = (x * 4)
          // sum both in order to find your index in the current row
          let i = currentRow + currentPixelInRow
          //getting the RGB
          let r = data[i]
          let g = data[i+1]
          let b = data[i+2]
          //convert RGB to hex
          let hex = '#' + [r, g, b].map(x => {
            const hex = x.toString(16)
            return hex.length === 1 ? '0' + hex : hex
          }).join('')
          //create array of tile colors
          // let fillColors = chipData.map(colorObj => colorObj.label);
          let lineWidth = Math.floor(size/2.25) > 0 ?  Math.floor(size/2.25) : 1
          //set up function to find nearest color from provided tile array
          let nearestColor = require('nearest-color').from(hexColors);
          //calculates postion of dice based on x and y in for loop
          let xnew = x * size
          let ynew = y * size
          //draws dice image at the desired locations
          let circle  = new Path2D()
          circle.arc(xnew+(size/2), ynew+(size/2), (size/2)-(lineWidth/2), 0, 2 * Math.PI, false)
          context.lineWidth = lineWidth
          context.strokeStyle = nearestColor(hex)
          context.stroke(circle)
        }
      }
      if(isGrid){
        let shift = size * 29 
        let w = size * numDiceWidth
        let h = size * numDiceHeight
        context.strokeStyle = 'black'
        context.beginPath()
        for (let x = shift; x < w; x += shift) {
					context.moveTo(x,0)
					context.lineTo(x,h)
				}
        for (let y = shift; y < h; y += shift) {
					context.moveTo(0,y)
					context.lineTo(w,y)
        }
        context.lineWidth = 15
        context.stroke()
      }
      setFinalUrl(canvas.toDataURL("image/jpg"));
      }
    }		
}

useEffect(()=> {
  if(finalUrl !== 'placeholder') {
    setIsDoneExporting(true)
    const link = document.createElement('a');
    link.href = finalUrl;
    link.setAttribute('download', 'file.png');
    document.body.appendChild(link);
    link.click();
    link.parentNode.removeChild(link);
  }
}, [finalUrl])

    return (
      <div className='grid-container'>
        <Helmet>
        <title>Perler Maker: A Generator to Create and Export Bead Art</title>
        <meta name="description" content="Upload photos and easily convert them into bead mosiacs. Choose colors to represent your beads. Adjust the number of beads in your mosiac. Export your design to a CSV file." />
        </Helmet>
  
      <div className='uploadArea'>
        <div className="logo">
              <Typography className='logoTitle' gutterBottom variant="h3" component="h3" color='primary'>
                Perler Maker |
              </Typography>
              <Typography className='logoDescription' gutterBottom variant="h5" component="h5" color='primary'>
                Create, Export Bead Art
              </Typography>
        </div>
        <div className='upload'>
          <ReactFileReader handleFiles={handleFiles} base64={true}>
              <Button
              variant="contained"
              color="primary"
              startIcon={<CloudUploadIcon />}
            >
              Upload
          </Button>
          </ReactFileReader>
        </div>
        <div className='toggle'>
          <FormControlLabel
          control={<Switch checked={isManual} onChange={handleChange} name="checkedA" />}
          label="Edit Colors"/>
        </div>
        {/* inputColorQTY */}
        {!isManual && <div className='position2'>
          <TextField
          id="outlined-number"
          label="Colors"
          type="number"
          defaultValue='5'
          InputLabelProps={{
            shrink: true,
          }}
          variant="outlined"
          onChange={(event) => onColorQtyChange(event)}
           />
        </div>}
        <div className='inputTile'>
          <TextField
          id="outlined-number"
          label="Beads"
          type="number"
          defaultValue='29'
          InputLabelProps={{
            shrink: true,
          }}
          variant="outlined"
          onChange={(event) => onChange(event)}
           />
        </div>
        {/* hexInput */}
        {isManual && <div className='position1'>
        <TextField id="hex" label="Hex" name="hex"
          value={hexInput}
          onChange={(event) =>  onHexInputChange(event)}
          error={hexInput!=='#' && !/^#([0-9A-F]{3}){1,2}$/i.test(hexInput)}
          helperText={hexInput!=='#' && !/^#([0-9A-F]{3}){1,2}$/i.test(hexInput) ? 'Invalid hex.' : ' '}
          variant="outlined"
        />
      </div>}
      {/* hexButton */}
        {isManual && <div className='position2'>
            <Button
              variant="contained"
              color="primary"
              onClick= {hexPush}
            >
              Add
            </Button>
        </div>}
        <div className='hex'>
            <Paper id='hexChild' component="ul" className={classes.root} >
            {chipData.map((data) => {
              
              return (
                <li key={data.key}>
                  <Chip
                    icon={<input className = 'color' type="color" disabled value={data.label}></input>}
                    label={data.label}
                    onDelete={isManual ? handleDelete(data) : null}
                    className={classes.chip}
                  />
                </li>
              );
            })}
            </Paper>
        </div>
      </div>
     
      <div className='widthInfoArea'>
          
      <div className='gridToggle'>
          <FormControlLabel
          control={<Switch checked={isGrid} onChange={() => setIsGrid(isGrid ? false : true)} name="checkedA" />}
          label="Grid"/>
        </div>

      {!isDoneTiling && file ?  
            <Typography className='loadingInfo' variant="body1" color="textSecondary" component="p">
              Beading...
            </Typography> : 

            <div></div>
      }

        <div className='widthInfo'>
       
          <Typography variant="body1" color="textSecondary" component="p">
            {Math.ceil(cmWidth / 1.6)} beads
          </Typography>
        </div>
      
      </div>
      <div className='lengthInfo'>
        <div className='length'>
        <Typography variant="body1" id="rotate-text" color="textSecondary" component="p">
          {Math.ceil(displayRatio * cmWidth / 1.6)} beads
        </Typography>
        </div>
      </div>

      <div ref={canvasParentRef} className ='imgParent'>
        <Card >
          <CardActionArea >
          <canvas ref={canvasRef}/> 
            <CardContent>
              <Typography variant="h6" color="textSecondary" component="h6">
                {Math.ceil(cmWidth / 1.6)*Math.ceil(displayRatio * cmWidth / 1.6)} beads
              </Typography>
              {csvData.length>0 ? chipData.map(chip => {

               return <div key={chip.key} className='tileCount'>
                      <input className = 'color' type="color" disabled value={chip.label}></input>
                      <Typography variant="body1" color="textSecondary" component="p">
                        {chip.label}, {csvData.flat().filter(x => x.toUpperCase() === chip.label).length} beads 
                      </Typography>
                      </div>
              })
               : <Typography variant="body1" color="textSecondary" component="p">
                  Upload a photo, adjust number of beads and colors, add desired hex codes.
                 </Typography>}
            </CardContent>
          </CardActionArea>
          <CardActions>
            <Button size="small" onClick={() => makeDownload()} color="primary" target="_blank">
              { isDoneExporting ? 'Export' : 'Exporting, please wait...'}
            </Button>
            <Button size="small" color="primary" href="https://taylorepskamp.github.io/diceify/" target='_blank'>
              Dice Mosaic App
            </Button>
          </CardActions>
        </Card>
      </div>
    </div>
    
    );
}

export default App;
